___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: #f95959;
  --shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--primary-color);
}

.container {
  background: white;
  position: fixed;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 0.5rem;
  padding: 10px;
  box-shadow: var(--shadow);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
}

.content-section h1 {
  text-align: center;
  width: 100%;
  height: 100%;
}


button {
  margin-top: 30px;
  background-color: #e3e3e3;
  cursor: pointer;
  border-radius: 0.5rem;
  box-shadow: var(--shadow);
}

button:hover {
  background-color: aquamarine;
}

input:focus {
  outline-color: 120px solid red;
  border-radius: 0.5rem;
}

.generated-cpf{
  font-size: 1.5em;
  color: var(--primary-color);
  text-align: center;
  font-weight: bold;
  letter-spacing: 3px;
